import React from 'react';
import './navbar.css';
import { Link } from "react-router-dom";



const NavBar = (props) => {

  // console.log(props.menu.length)

  let menuItems = (props) => {

    var i = 0;
    let year_list = [];
    let menu_list = [];
    var	handleToUpdate2	=	props.handleToUpdate2;


    for (i = 0; i < props.menu.length; ++i){
      let value = props.menu[i].year;

      if (year_list.includes(props.menu[i].year) === false) {
    year_list.push(props.menu[i].year)
    menu_list.push(
    <li>
      <Link to='/byYearList'>
        <a href={props.menu[i].year} 
        onClick={function(e){e.currentTarget.closest('nav').classList.toggle('active'); handleToUpdate2(value)}} >
        {props.menu[i].year}
        </a>
        </Link></li>
      )
    } }
  console.log(year_list)
  return <>{menu_list}</>
  }


 


    return (
    <nav id="navbar" className='navbar test'>
    <button className="burgerIcon" onClick={function(e){e.currentTarget.parentElement.classList.toggle('active');}}><span className="line"></span></button>   
    <ul>
    <Link to='/main'>
      <li onClick={function(e){e.currentTarget.closest('nav').classList.toggle('active');}}>SHOW ALL</li>
      </Link>
    {menuItems(props)}
    </ul>
    <Link to='/app'>
    <a href='/app' id='logo-container'><img id="logo" src={props.logo}/> </a>
    </Link>


    </nav>
    )
};


export default NavBar;