import React from 'react';
import './imgGrid.css';
import { Link } from "react-router-dom";



const ImgGrid = (props) => {


    let mapThru = (props) => {

        var	handleToUpdate	=	props.handleToUpdate;
        let i = 0;
        let tour_list = [];

        
        for (i = 0; i < props.database.length; ++i){
        let value = props.database[i].key;
        tour_list.push(
            <>
                <Link 
                to={props.database[i].link}
                >         
                    <div 
                    class='imgGridImg column'
                    style={{ backgroundImage:`url(${props.database[i].poster})`}} 
                    onClick={() => handleToUpdate(value)} src={props.database[i].poster}>
                    </div>
                </Link>
            </>
            )
        }

        return <>
        {tour_list}
        </> 
    }

    return (
    <div id="imgGrid" class='row'>
       {mapThru(props)}
    </div>
    )
};


export default ImgGrid;